<template>
  <header class="header">
    <Transition name="header__notes">
    <div class="header__notes" v-show="header === true">
      <button class="header__lang" @click="switchLang">{{ $i18n.locale }}</button>
      <h1 class="header__title">{{$t('notes')}}</h1>
      <button class="header__search" @click="header = false">
        <img src="@/assets/images/search.svg" alt="" />
      </button>
    </div>
    </Transition>
    <Transition name="header__form">
    <div class="header__form" v-show="header === false">
      <button class="haeder__back" @click="header = true, search = ''">
        <img src="@/assets/images/back.svg" alt="" />
      </button>
      <div class="container">
        <input v-model="search" type="text" class="header__input" :placeholder="$t('searchInput')" />
      </div>
      <button class="haeder__clear" @click="search = ''">
        <img src="@/assets/images/clear.svg" alt="" />
      </button>
    </div>
    </Transition>
  </header>
</template>

<script>
export default {
  data() {
    return {
      header: true,
      search: ''
    };
  },
  methods: {
    switchLang(){
      localStorage.lang = this.$i18n.locale
      this.$i18n.locale == 'ru' ? this.$i18n.locale = 'uz' : this.$i18n.locale = 'ru'
    }
  },
  watch: {
    search(val){
      this.$emit('searchValue', val)
    },
    header(val){
       this.$emit('header', val)
    }
  }
};
</script>

<style >
.header {
  background: #f3edf7;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.3),
    0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  padding: 14px 16px;
  height: 70px;
  overflow: hidden;
}

.header__notes,
.header__form {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__lang {
  font-size: 22px;
  font-weight: 700;
}

.header__title {
  color: #1c1b1f;
  font-size: 22px;
  font-weight: 400;
  line-height: 28px;
}

.header__input {
  display: block;
  width: 100%;
  color: #9d9d9d;
  font-size: 16px;
  line-height: 20px;
}

.header__notes-enter-active, .header__notes-leave-active {
  transition: 500ms linear;
}

.header__notes-enter-from, .header__notes-leave-to {
  opacity: 0;
  transform: translateY(-200px);
} 

.header__notes-enter-to, .header__notes-leave-from {
  opacity: 1;
  transform: translateY(0px);
} 

.header__form-enter-active, .header__form-leave-active {
  transition: 500ms linear;
}

.header__form-enter-from, .header__form-leave-to {
  opacity: 0;
  transform: translateY(200px);
} 

.header__form-enter-to, .header__form-leave-from {
  opacity: 1;
  transform: translateY(0px);
} 

</style>